.footCont h2 {
    font-family: 'Roboto', sans-serif;
    font-weight: 2em;
    color: var(--titre-color);
    text-align: center;
    margin-bottom: 30px;
}

.Conteneur-contact-p {
    padding: 20px;
    background-color: var(--section-background-color);
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.contact-container-p {
    padding: 40px;
    border-radius: 8px;
    max-width: 600px;
    height: auto;
    margin: 20px;
    opacity: 1; 
    transform: translateY(0); 
}

.contact-container-p.loaded {
    opacity: 1; 
    transform: translateY(0); 
}

.contact-form-p {
    display: flex;
    flex-direction: column;
}

.form-row {
    display: flex;
    justify-content: space-between;
    gap: 40px;
}

.form-group-p {
    flex: 1;
    display: flex;
    flex-direction: column;
    padding-bottom: 10px;
}

.form-group-p label {
    color: var( --titre-color);
    font-family: 'Roboto', sans-serif;
    margin-bottom: 8px;
    font-size: 16px;
}

.form-group-p input,
.form-group-p textarea {
    padding: 12px;
    font-size: 14px;
    border-radius: 4px;
    border: 1px solid var(--border-form);
    background-color: var(--section-background-color);
    color: var(--titre-color);
}

.form-group-p input:focus,
.form-group-p textarea:focus {
    border-color: #2769ab;
    outline: none;
}

textarea {
    resize: vertical;
}

.submit-btn-p {
    margin-top: 20px;
    padding: 15px;
    font-size: 16px;
    background-color: #00bfff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.submit-btn-p:hover {
    background-color: #007acc;
}

@media (max-width: 600px) {
    .form-row {
        flex-direction: column;
    }
}

.contact-rapide-p img {
    max-width: 100%;
    height: auto;
    padding-top: 50px;
    animation: bounce 2s infinite;
}

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        transform: translateY(0);
    }
    40% {
        transform: translateY(-30px);
    }
    60% {
        transform: translateY(-15px);
    }
}

@media (max-width: 376px) {
    .contact-container {
        width: 250px;
        display: flex;
        align-items: center;
    }
}
@media (max-width: 480px) {
    .contact-container {
        max-width: 480px;
        display: flex;
        align-items: center;
    }
}