
.navbar {
    border-radius: 12px;
    background: var(--background-color);
    border: 1px solid var(--text-color);
    backdrop-filter: blur(16px) saturate(120%);
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 20px 80px;
    position: relative;
  }
  
  
  .navbar .navLinks {
    display: flex;
    gap: 15px;
  }
  

  .themeToggle {
    margin-right: 30px;
    margin-left: 40px;
  }
  

  .navbar a {
    font-family: 'Poppins', sans-serif;
    text-decoration: none;
    color: var(--text-color);
    position: relative;
    padding-bottom: 5px;
  }
  
  .navbar a:hover::after,
  .navbar a.active::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 2px;
    background-color: var(--text-color);
  }
  
  .navbar button{
    display: none;
  }
  
  
  

  @media (max-width: 768px) {
    .navbar .navLinks {
      display: none; 
    }
  
    .navbar button {
      display: block; 
    }

  .menuIcon {
    display: none; 
    font-size: 2rem;
    cursor: pointer;
    position: absolute;
    top: 20px; 
    right: 20px; 
    transform: translateY(-40%);
    color: var(--text-color) !important; 
    background: var(--background-color); 
    border: 2px solid var(--text-color); 
    border-radius: 50%; 
    padding: 10px; 
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); 
  }
  /* Side menu styles */
  .sideMenu {
    position: fixed;
    top: 0;
    right: 0;
    height: 100%;
    width: 250px;
    background: var(--background-color);
    border-left: 1px solid var(--text-color);
    display: flex;
    flex-direction: column;
    padding: 20px;
    z-index: 1000; 
    box-shadow: -2px 0 5px rgba(0, 0, 0, 0.5); 
  }
  
  .sideMenu a {
    margin-bottom: 20px;
    color: var(--text-color);
    font-family: 'Poppins', sans-serif;
    text-decoration: none;
    position: relative;
  }
  .sideMenu a:hover::after,
  .sideMenu a.active::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 2px;
    background-color: var(--text-color);
    width: 40%;
  }
  }
  @media (max-width: 480px){
    .sideMenu a {
      margin-bottom: 20px;
      color: var(--text-color);
      font-family: 'Poppins', sans-serif;
      text-decoration: none;
      position: relative;
    }
    .sideMenu a:hover::after,
  .sideMenu a.active::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 2px;
    background-color: var(--text-color);
    width: 40%;
  }
  }
  @media (max-width: 376px){
    .themeToggle {
      margin-right: 15px;
      margin-left: 10px;
    }
    .sideMenu a {
      margin-bottom: 20px;
      color: var(--text-color);
      font-family: 'Poppins', sans-serif;
      text-decoration: none;
      position: relative;
    }
    .sideMenu a:hover::after,
  .sideMenu a.active::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 2px;
    background-color: var(--text-color);
    width: 40%;
  }
  }